<template>
  <div class="mine_container">
    <div class="top_header">
      <header-view :titles="title"></header-view>
    </div>
    <div class="car_list">
     <ul>
       <li class="car_list_item" v-for="(item ,index) in shopList">
        <div class="shop_name_edit">
           <div class="left_shop">
             <span class="shop_icon"></span>
             <span class="shop_name">{{item.store_name}}</span>
           </div>
           <p class="edit">编辑</p>
        </div>
         <div class="buy_good">
           <div class="left_select">
             <div class="cbselect"></div>
           </div>
           <img :src="item.goods_image_url" alt="" class="good_img">
           <div class="good_desc">
              <div class="good_name">
               {{item.goods_name}}
              </div>
              <div class="good_property">
                 <p class="one">商品的参数详情</p>
                 <p class="two">￥{{item.goods_price }} 元</p>
                <div class="fan_num">
                  <span>返现<span class="fan">{{item.goods_price*item.goods_return_rate }}</span>元</span>
                  <span>x {{item.goods_num }}</span>
                </div>
              </div>
           </div>
         </div>
       </li>

     </ul>
    </div>

    <div class="total">
      <div class="all_select">
        <div class="icon_select"></div>
        <p>全选</p>
      </div>
      <div class="total_money">合计：$123.0</div>
      <div class="topay">结算</div>
    </div>
  </div>
</template>
<script>
  import HeaderView from '../../components/header-view'
  export default {
    data(){
      return{
        title:'购物车',
        shopList:[],
        sum:0
      }
    },
    components:{
      HeaderView
    },
    methods:{
      //获取购物车商品列表
      getShopCar(){
        let token=localStorage.getItem('token');
        let that=this;
        this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=member_cart&op=cart_list&token="+token, //跨域请求接口
          {
            jsonp:'callback',
            jsonpcallback	:'cart',
          }).then(function(res){

          console.log('=====购物车token========'+JSON.parse(res.bodyText))
          that.shopList=JSON.parse(res.bodyText).datas.cart_list;
          that.sum=JSON.parse(res.bodyText).datas.sum;
        },function(){
          console.log("购物车请求失败！！！")
        });
      }
    },
    created(){
      //显示底部导航栏
      this.$store.commit("showFooterTab",true);
      this.getShopCar();
    }
  }
</script>
<style scoped>
@import "../../css/reset.css";
.mine_container{
  background-color: #f9f9f9;
}
.top_header{
  width: 100%;
  position: fixed;
  left: 0;
  top:0;
  z-index: 3;
}
  .car_list{
    margin-top: 4.5rem;
    margin-bottom: 10.5rem;
    width: 100%;
  }
  .total{
    display: flex;
    width: 100%;
    height: 4rem;
    position: fixed;
    left: 0;
    bottom: 5rem;
    background-color: #f9f9f9;
    z-index: 3;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
  }
  .all_select{
    display: flex;
    margin-left: 1.5rem;
    align-items: center;
  }
  .icon_select{
     width: 1.8rem;
     height: 1.8rem;
    background-image: url("../../assets/images/cb_checked.png");
    background-repeat: no-repeat;
    background-size: 1.8rem 1.8rem;
  }
.all_select p{
  margin-left: 0.5rem;
  font-family: "SimSun", "宋体";
  color: #333333;
  font-size: 2.4rem;
}
.total_money{
  font-family: "SimSun", "宋体";
  color: #333333;
  font-size: 2.4rem;
  font-weight: 700;
}
  .topay{
    margin-right: 2rem;
    border: 0.1rem solid #0BA062;
    padding: 0.4rem 1.5rem;
    background-color: #0BA062;
    color: white;
  }
  .car_list_item{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 20rem;
    background-color: #fff;
    margin-bottom: 0.5rem;
  }
  .shop_name_edit{
    display: flex;
    width: 100%;
    height: 4rem;
    background-color: #ffffff;
    box-sizing: border-box;
    padding: 0 1.5rem;
    justify-content: space-between;
    align-items: center;
    border-bottom: 0.1rem solid #f9f9f9;
  }
  .left_shop{
     display: flex;
     height: 100%;
    align-items: center;
  }
  .shop_icon{
    display: inline-block;
    width: 2.4rem;
    height: 2.4rem;
    background-image: url("../../assets/images/shop_icon.png");
    background-repeat: no-repeat;
    background-size: 2.4rem 2.4rem;
  }
  .shop_name{
    margin-left: 0.8rem;
    font-family: "SimSun", "宋体";
    font-size: 2.6rem;
  }
  .edit{
    font-family: "SimSun", "宋体";
    font-size: 2.6rem;
    color: #0BA062;
  }
  .buy_good{
    display: flex;
    flex: 1;
    width: 100%;
    align-items: center;
    box-sizing: border-box;
    padding: 0 1.5rem;
  }
  .left_select{
    display: flex;
    width: 4rem;
    height: 100%;
    align-items: center;
    justify-content: center;
  }
  .cbselect{
    width: 1.8rem;
    height: 1.8rem;
    background-image: url("../../assets/images/cb_checked.png");
    background-repeat: no-repeat;
    background-size: 1.8rem 1.8rem;
  }
  .good_img{
    margin-left: 1.5rem;
    display: block;
    width: 10rem;
    height: 10rem;
  }
  .good_desc{
    display: flex;
    flex: 1;
    flex-direction: column;
    height: 10rem;
    margin-left: 1rem;

  }
  .fan_num{
    display: flex;
    justify-content: space-between;

  }
  .fan{
    color: #0BA062;
  }
  .good_name{
    display: block;
    width: 10rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 3rem;
    line-height: 3rem;
    color: #333333;
    font-family: sans-serif;
    font-weight: 400;
    font-size: 2.5rem;
  }
  .one{
    height: 2rem;
    line-height: 2rem;
    color: #666666;
    font-size: 1.8rem;
    font-family: sans-serif;
  }
  .two{

    height: 2rem;
    line-height: 2rem;
    color: #333333;
    font-size: 2.3rem;
    font-family: sans-serif;
  }
  .fan_num{
    height: 2rem;
    line-height: 2rem;
    color: #333333;
    font-size: 2.2rem;
    font-family: sans-serif;
  }
</style>



