/* harmony default export */ var regist_regist = ({
  data: function data() {
    return {
      title: "注册",
      mobile: '',
      pwd: '',
      identify_code: '',
      hasRegist: false //是否注册过
    };
  },

  watch: {
    //通过观察手机号，来检查该手机号是否被注册过
    mobile: {
      handler: function handler(val, oldval) {
        if (val.length === 11) {
          this.checkMobile();
        }
      }
    },
    deep: true
  },
  components: {
    HeaderView: src_components_header_back
  },
  methods: {
    back: function back() {
      this.$router.go(-1);
    },

    //检查账号是否注册
    checkMobile: function checkMobile() {
      if (!utils.vaildPhone(this.mobile)) {
        return;
      }
      this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=login&op=checkMobile", //跨域请求接口
      {
        params: {
          username: this.mobile
        },
        jsonp: 'callback',
        jsonpcallback: 'checkUserName'
      }).then(function (res) {
        var response = JSON.parse(res.bodyText);
        if (response.code === 5) {
          utils.ToastUtil(response.msg);
          this.hasRegist = true;
        } else {
          this.hasRegist = false;
        }
      }, function () {
        utils.ToastUtil("请求失败！！！");
      });
    },

    //发送验证码
    sendCode: function sendCode() {
      if (this.hasRegist) {
        utils.ToastUtil("手机号已经被注册过了");
        return;
      }
      this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=sms&op=index", //跨域请求接口
      {
        params: {
          mobile: this.mobile
        },
        jsonp: 'callback',
        jsonpcallback: 'verifyCode'
      }).then(function (res) {
        var response = JSON.parse(res.bodyText);
        utils.ToastUtil(response.msg);
      }, function () {
        utils.ToastUtil("请求失败！！！");
      });
    },

    //注册
    regist: function regist() {
      if (this.hasRegist) {
        utils.ToastUtil("手机号已经被注册过了");
        return;
      }
      if (!utils.vaildPhone(this.mobile)) {
        return;
      }
      if (this.pwd.length === 0) {
        utils.ToastUtil("请输入手机号");
        return;
      }
      this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=login&op=register", //跨域请求接口
      {
        params: {
          member_name: this.mobile,
          member_password: this.pwd,
          invite_code: "",
          sms_code: this.identify_code
        },
        jsonp: 'callback',
        jsonpcallback: 'register'
      }).then(function (res) {
        var response = JSON.parse(res.bodyText);
        localStorage.setItem('token', response.datas.token.token);
      }, function () {
        utils.ToastUtil("请求失败！！！");
      });
    }
  }

});
