/* harmony default export */ var tab_switch = ({
  props: {
    showFooter: {
      type: Boolean,
      default: true
    }
  },
  data: function data() {
    return {};
  },

  methods: {
    switchTab: function switchTab(index) {
      switch (index) {
        case 1:
          //             修改vuex中currentPage的值为1,默认为1
          this.$store.commit("switchTab", 1);
          router.push({ path: '/index' });
          break;
        case 2:
          //             修改vuex中currentPage的值为2
          this.$store.commit("switchTab", 2);

          router.push({ path: '/shop' });

          break;
        case 3:
          //             修改vuex中currentPage的值为3
          this.$store.commit("switchTab", 3);
          router.push({ path: '/category' });
          break;
        case 4:
          //             修改vuex中currentPage的值为4
          this.$store.commit("switchTab", 4);
          router.push({ path: '/shopCar' });
          break;
        case 5:
          this.judgeLogon();

          break;
      }
    },

    //      判断是否登录
    judgeLogon: function judgeLogon() {
      var token = localStorage.getItem('token');
      console.log("=====islogin=======" + token);
      if (!token) {
        router.push({ path: '/login' });
      } else {
        // 修改vuex中currentPage的值为4
        this.$store.commit("switchTab", 5);
        router.push({ path: '/mine' });
      }
    }
  },
  created: function created() {

    console.log(this.showFooter);
  }
});
