       <template lang="html">
         <div class="detail_container">
          <div class="top_header">
            <header-back :titles="title" @back="back"></header-back>
          </div>
            <div class="content">
              <!--banner-->
              <div class="banner">
                <mt-swipe :auto="4000" @change="handleChange">
                  <mt-swipe-item  v-for="item in datas.goods_image_header">
                    <img :src="item" alt="" class="banner_img" >
                  </mt-swipe-item>
                </mt-swipe>
              </div>
              <!--商品参数-->
              <div class="good_param">
                <p class="good_name">{{datas.goods_name}}</p>
                <p class="good_price">￥&nbsp;{{datas.goods_price}}</p>
                <p class="return_cash">返现￥{{backCash }}元</p>
                <div class="origin_address_storge">
                  <p class="address">发货地 :&nbsp;{{datas.storeInfo.area_info}}</p>
                  <p class="storges">库存 {{datas.goods_storage}}件</p>
                </div>
              </div>
              <!--导航栏切换-->
              <good-detail-switch @switchIndex="switchIndex" ></good-detail-switch>
              <!--左侧导航栏-->
              <detail-list v-if="flag" :datas="datas"></detail-list>
              <!--右侧导航栏-->
              <product-detail v-show="!flag" :datas="datas"></product-detail>
            </div>
             <!--footer-->
            <div class="footer_container">
              <add-buy @addshopCar="addshopCar" @buy="buy"></add-buy>
            </div>
           <!--购物车dialog-->
            <div class="shopcarlist" v-if="showCar">
               <div class="gc_info">
                 <img src="" class="good_image">
                 <div class="price_storge">
                   <p class="price">￥122.00</p>
                   <p class="storages">库存0</p>
                   <p class="select_size_color">选择颜色、尺寸</p>
                 </div>
                 <span class="close"></span>
               </div>
                <div class="good_size">
                  <p class="p_size">尺寸</p>
                </div>
              <div class="good_color">
                <p class="p_color">颜色</p>
              </div>
            </div>
           <!--购买dialog-->
           <div class="buy_dialog" v-if="showBuy">
             <div class="buy_dialog">56566</div>
           </div>
         </div>
       </template>
       <script>
         import HeaderBack from '../../components/header_back'
         import AddBuy from './buy_add'
         import GoodDetailSwitch from './good-detail-switch'
         import ProductDetail from './product_detail_list'
         import DetailList from './detal_goodlist.vue'
         export default {
           data(){
             return{
             title:'商品详情',
               flag:true,
               datas:{},
               showCar:false,
               showBuy:false
           }},
           computed:{
             backCash(){
               return this.datas.goods_return_rate*this.datas.goods_price;
             }
           },
           components:{
             HeaderBack,
             AddBuy,
             GoodDetailSwitch,
             ProductDetail,
             DetailList
           },

           methods:{
             //banner点击事件
             handleChange(index) {
              console.log('item   '+index)
             },
             back(){
               this.$router.go(-1);
             },
             addshopCar(){
               this.showCar=!this.showCar;
             },
             buy(){
               console.log("立即购买")
             },
             switchIndex(index){
               if(index===1){//产品参数
                  this.flag=true
               }else{//商品详情
                 this.flag=false
               }
             },
             initDetail(){
                     let token=localStorage.getItem('token');
               let that=this;
               this.$http.jsonp('http://gbfmall.slh8.com/mobile/index.php?act=goods&op=goodsDetail',
                 {
                   params: {
                     goods_id:this.$route.params.id,
                     token:token
                   },
                   jsonp:'callback',
                   jsonpcallback	:'detail',
                 }).then(function(res){

                  that.datas=JSON.parse(res.bodyText).datas;
               },function(){
                 Toast("商品详情数据请求失败！！！")
               });
             }
           },
           created(){
             this.initDetail();
             console.log("======goodId===="+this.$route.params.id)
           }
         }
       </script>
       <style lang="css" scoped >
         @import "../../css/reset.css";
         @import "detail.css";
       </style>



